import React from "react";
import ReactDOM from "react-dom";
import "./assets/index.css";
import App from "./App";

import { BrowserRouter as Router } from "react-router-dom";
import { BookProvider } from "./context/context";

ReactDOM.render(
  <React.StrictMode>
    <BookProvider>
      <Router>
        <App />
      </Router>
    </BookProvider>
  </React.StrictMode>,
  document.getElementById("root")
);

